import React, { useState } from 'react'
import Breadcrum from "./Partials/Breadcrum"

import { Link, useNavigate } from 'react-router-dom'
export default function ForgetPassword2() {
    let [otp, setOtp] = useState("")
    let [show, setShow] = useState(false)
    let [message, setMessage] = useState("")

    let navigate = useNavigate()

    function getInputData(e) {
        setShow(false)
        setMessage("")
        setOtp(e.target.value)
    }
    async function postData(e) {
        e.preventDefault()
        let response = await fetch("/api/user/forget-password-2", {
            method: "POST",
            headers: {
                "content-type": "application/json"
            },
            body: JSON.stringify({ username: localStorage.getItem("password-reset-user"),otp:otp })
        })
        response = await response.json()
        console.log(response);
        
        if (response.result === "Done") {
            navigate("/forget-password-3")
        }
        else {
            setShow(true)
            setMessage(response.reason)
        }
    }
    return (
        <>
            <Breadcrum title="Login Section" />

            <div className="container my-3">
                <div className="row">
                    <div className="col-lg-8 col-md-10 col-sm-11 m-auto">
                        <h5 className='bg-primary text-center p-2 text-light'>Forget Password</h5>
                        <form onSubmit={postData}>
                            <div className=" mb-3">
                                <input type="text" name="otp" onChange={getInputData} className={`form-control border-2 ${show && message ? 'border-danger' : 'border-primary'}`} placeholder='Enter OTP which is Sent on Your Registered Email Address' />
                                {show && message ? <p className='text-danger'>{message}</p> : ""}
                            </div>

                            <div className="mb-3">
                                <button type="submit" className='btn btn-primary w-100'>Submit</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}
