import React, { useState } from 'react'
import Breadcrum from "./Partials/Breadcrum"

import formValidator from "./FormValidators/formValidator"
import { Link, useNavigate } from 'react-router-dom'
export default function ForgetPassword3() {
    let [data, setData] = useState({
        password: "",
        confirmPassword: "",
    })
    let [errorMessage, setErrorMessage] = useState({
        password: ""
    })
    let [show, setShow] = useState(false)
    let [message, setMessage] = useState("")
    let navigate = useNavigate()

    function getInputData(e) {
        let { name, value } = e.target
        setMessage("")
        if (name !== "confirmPassword") {
            setErrorMessage((old) => {
                return {
                    ...old,
                    [name]: formValidator(e)
                }
            })
        }
        setData((old) => {
            return ({
                ...old,
                [name]: value
            })
        })
    }
    async function postData(e) {
        e.preventDefault()

        let error = Object.values(errorMessage).find(x => x !== "")
        if (error)
            setShow(true)
        else if(data.password!==data.confirmPassword){
            setShow(true)
            setErrorMessage((old)=>{
                return{
                    ...old,
                    'password':"Password and Confirm Password Doesn't Matched"
                }
            })
        }
        else {
            let response = await fetch("/api/user/forget-password-3", {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify({ username: localStorage.getItem("password-reset-user"), password: data.password })
            })
            response = await response.json()
            if (response.result === "Done"){
                localStorage.removeItem("password-reset-user")
                navigate("/login")
            }
            else {
                setShow(true)
                setMessage(response.reason)
            }
        }
    }
    return (
        <>
            <Breadcrum title="Login Section" />

            <div className="container my-3">
                <div className="row">
                    <div className="col-lg-8 col-md-10 col-sm-11 m-auto">
                        <h5 className='bg-primary text-center p-2 text-light'>Forget Password</h5>
                        <form onSubmit={postData}>

                            <div className="mb-3">
                                <input type="password" name="password" onChange={getInputData} className={`form-control border-2 ${show && errorMessage.password ? 'border-danger' : 'border-primary'}`} placeholder='Password' />
                                {show && errorMessage.password ? <p className='text-danger'>{errorMessage.password}</p> : ""}
                            </div>

                            <div className="mb-3">
                                <input type="password" name="confirmPassword" onChange={getInputData} className={`form-control border-2 ${show && errorMessage.password ? 'border-danger' : 'border-primary'}`} placeholder='Confirm Password' />
                            </div>

                            <div className="mb-3">
                                <button type="submit" className='btn btn-primary w-100'>Reset</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}
